<template>
  <div class="activate-verify-code-container border-box">
    <div class="tip-box f-i-c">
      <img src="@/assets/images/verify_code_tip.png" alt="">
      <p>输入激活码</p>
    </div>

    <div class="input-code-box f-c">
      <van-field v-model="activateVerifyCode" clearable placeholder="输入激活码" />
    </div>

    <div class="operate-box f-row-between-c">
      <van-button
        text="上一步"
        round
        hairline
        plain
        color="#F4BA41"
        @click="lastStep"
      />
      <van-button
        text="下一步"
        round
        color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"
        @click="nextStep"
      />
    </div>

    <div class="opening-explain">
      如果宝宝所在机构未开通青豆服务
      您可选择自行开通 <span>青豆会员</span>
    </div>

    <div @click="buyGift" class="buy-gift-box">
      <img src="@/assets/images/verify_code_buy_gift.png" alt="">
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { Field, Button } from 'vant';

Vue.use(Field).use(Button);

export default {
  data() {
    return {
      activateVerifyCode: ''
    }
  },

  created() {
    document.title = '青豆家长';
  },

  methods: {
    lastStep() {
      console.log( '上一步' );
    },

    nextStep() {
      console.log( '下一步' );
    },

    buyGift() {
      console.log( '买礼盒' );
    }
  }
}
</script>

<style lang="scss" scoped>
.activate-verify-code-container {
  width: 100%;
  min-height: 100%;
  padding: 30px 20px 0;
  background-color: #fff;

  .tip-box {
    width: 100%;
    height: 60px;

    img {
      width: 60px;
    }
    p {
      font-size: 18px;
      font-weight: 500;
      color: rgba(34,34,34,1);
      margin-left: 15.5px;
    }
  }

  .input-code-box {
    margin-top: 30px;
    width: 100%;
    height: 44px;
    border: 1px solid rgba(221,221,221,1);
    border-radius: 22px;

    .van-cell {
      padding: 0 16px;
      width: calc(100% - 20px);
    }
  }

  .operate-box {
    margin-top: 75px;
    width: 100%;
    height: 45px;

    button {
      width: calc(50% - 7.5px);
    }
  }

  .opening-explain {
    margin: 90px 32px 0;
    width: calc(100% - 100px);
    font-size: 14px;
    color: rgba(51,51,51,1);
    line-height: 2;
    text-align: center;

    span {
      color: #E64E50;
      font-size: 16px;
      font-weight: bold;
    }
  }

  .buy-gift-box {
    margin-top: 20px;
    width: 100%;

    img {
      width: 100%;
    }
  }
}
</style>


